// Espessura
$width-none: 0;
$width-sm: 1px;
$width-md: 2px;
$width-lg: 4px;

$widths: (
  width-none: $width-none,
  width-sm: $width-sm,
  width-md: $width-md,
  width-lg: $width-lg,
);

// Bordas
$border-solid-none: var(--surface-width-none) solid var(--color-secondary-06);
$border-solid-sm: var(--surface-width-sm) solid var(--color-secondary-06);
$border-solid-md: var(--surface-width-md) solid var(--color-secondary-06);
$border-solid-lg: var(--surface-width-lg) solid var(--color-secondary-06);
$border-dashed-none: var(--surface-width-none) dashed var(--color-secondary-06);
$border-dashed-sm: var(--surface-width-sm) dashed var(--color-secondary-06);
$border-dashed-md: var(--surface-width-md) dashed var(--color-secondary-06);
$border-dashed-lg: var(--surface-width-lg) dashed var(--color-secondary-06);

$borders: (
  border-solid-none: $border-solid-none,
  border-solid-sm: $border-solid-sm,
  border-solid-md: $border-solid-md,
  border-solid-lg: $border-solid-lg,
  border-dashed-none: $border-dashed-none,
  border-dashed-sm: $border-dashed-sm,
  border-dashed-md: $border-dashed-md,
  border-dashed-lg: $border-dashed-lg,
);

// Cantos
$rounder-none: 0;
$rounder-sm: 4px;
$rounder-md: 8px;
$rounder-lg: 16px;
$rounder-pill: 999em;

$rounders: (
  rounder-none: $rounder-none,
  rounder-sm: $rounder-sm,
  rounder-md: $rounder-md,
  rounder-lg: $rounder-lg,
  rounder-pill: $rounder-pill,
);

// Opacidade
$opacity-none: 0;
$opacity-default: 1;
$opacity-xs: 0.16;
$opacity-sm: 0.3;
$opacity-md: 0.45;
$opacity-lg: 0.65;
$opacity-xl: 0.85;

$opacities: (
  opacity-none: $opacity-none,
  opacity-default: $opacity-default,
  opacity-xs: $opacity-xs,
  opacity-sm: $opacity-sm,
  opacity-md: $opacity-md,
  opacity-lg: $opacity-lg,
  opacity-xl: $opacity-xl,
);

// Deslocamento
$offset-none: 0;
$offset-sm: 1px;
$offset-md: 3px;
$offset-lg: 6px;
$offset-xl: 9px;

$offsets: (
  offset-none: $offset-none,
  offset-sm: $offset-sm,
  offset-md: $offset-md,
  offset-lg: $offset-lg,
  offset-xl: $offset-xl,
);

// Suavidade
$blur-none: 0;
$blur-sm: 1px;
$blur-md: 3px;
$blur-lg: 6px;

$blurs: (
  blur-none: $blur-none,
  blur-sm: $blur-sm,
  blur-md: $blur-md,
  blur-lg: $blur-lg,
);

// Sombra
$shadow-none: none;
$shadow-sm: var(--surface-offset-none) var(--surface-offset-md)
  var(--surface-blur-lg)
  rgba(var(--rgb-secondary-09), var(--surface-opacity-xs));
$shadow-md: var(--surface-offset-md) var(--surface-offset-lg)
  var(--surface-blur-lg)
  rgba(var(--rgb-secondary-09), var(--surface-opacity-xs));
$shadow-lg: var(--surface-offset-lg) var(--surface-offset-xl)
  var(--surface-blur-lg)
  rgba(var(--rgb-secondary-09), var(--surface-opacity-xs));

$shadows: (
  shadow-none: $shadow-none,
  shadow-sm: $shadow-sm,
  shadow-md: $shadow-md,
  shadow-lg: $shadow-lg,
);

// Scrim Foco
$overlay-scrim: rgba(var(--rgb-secondary-09), var(--surface-opacity-md));

// Scrim Legibilidade
$overlay-text: linear-gradient(rgba(var(--rgb-secondary-01), var(--surface-opacity-none)),
rgba(var(--rgb-secondary-09), var(--surface-opacity-default)));
